.Button {
  outline: none;
  cursor: pointer;
  font: inherit;
  padding: 10px 10%;
  margin: 10px 0;
  border-radius: 5px;
}

.Button-red {
  background-color: rgb(146, 96, 96);
  color: rgb(252, 214, 214);
}

.Button-blue {
  border: none;
  background-color: #209cee;
  color: white;
  width: 100%;
}

.Button-transparent {
  background: transparent;
  color: #209cee;
  border-color: #209cee;
}

.Button-orange {
  background-color: rgb(184, 73, 0);
  color: rgb(248, 183, 97);
}
