.DarkBox {
  position: fixed;
  width: 100%;
  height: 50%;
  top: 50%;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 2;
}

.DarkBox-blue {
  background-color: rgb(83, 94, 192);
}

.DarkBox-red {
  background-color: rgb(228, 79, 79);
}

.DarkBox-green {
  background-color: rgb(73, 172, 111);
}

.DarkBox-black {
  background-color: rgba(0, 0, 0, 0.8);
}
